import React, { useState } from 'react';
import AppSearchBar from './AppSearchBar';
import CssBaseline from '@mui/material/CssBaseline';
import ActivityCard from './ActivityCard';
import Grid2 from '@mui/material/Unstable_Grid2';
import { searchForCharacter } from './DestinyAPI';

async function Results(){
    const searchParams = window.location.hash;
    const bungieName = searchParams.split("=")[1];

    //console.log(bungieName);
    let response = await searchForCharacter(bungieName);
    console.log('Results');
    console.log(response);

    return (
        <>
        <CssBaseline/>
        <AppSearchBar />
        <Grid2 container rowSpacing={5} spacing={8} sx={{p:6, width: 1}}>
            {Array.from(Array(14)).map((_, index) => (
                <Grid2 key={index}>
                    <ActivityCard data={bungieName}/>
                </Grid2>
            ))}
        </Grid2>
        </>
    );
  };

export default Results;