import React, { useState } from 'react';
import DoubleArrowIcon from '@mui/icons-material/DoubleArrow';
import SearchIcon from '@mui/icons-material/Search';
import { Paper, InputBase, IconButton, Card, CardMedia, Container } from '@mui/material';
import { useNavigate } from "react-router-dom";
import d2logo from '../images/d2logo2.png';
import { Box, AppBar, Toolbar, Typography } from '@mui/material';
import { useTheme, ThemeProvider, createTheme } from '@mui/material/styles';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import CssBaseline from '@mui/material/CssBaseline';
import { Link } from "react-router-dom";

const ColorModeContext = React.createContext({ toggleColorMode: () => {} });

const DestinyLogo = () => {
    return(
        <Link to={`/`}><img src={d2logo} width={84} height={60}></img></Link>
    );
};

function AppSearchBar(){
    const [searchField, setSearchField] = useState("");
    const [searchShow, setSearchShow] = useState(false);
    const theme = useTheme();
    const colorMode = React.useContext(ColorModeContext);

    let navigate = useNavigate();

    const handleChange = e => {
        setSearchField(e.target.value);
    };

    const handleSumbit = (e) => {
        if(searchField===""){
            console.log("Empty");
            
        }
        else {
            //console.log(searchField);
            navigate(`/search?n=${searchField}`);
        }
    };

    
    return (
        <>
            <CssBaseline/>
            <Container maxWidth={false} disableGutters sx={{
                            bgcolor: 'background.default',
                            color: 'text.primary',
                        }}> 
                <Box sx={{
                    display: 'flex',
                    width: '100%',
                    justifyContent: 'center',
                    bgcolor: 'background.default',
                    color: 'text.primary',
                }}>
                    <AppBar position="static">
                        <Toolbar>
                            <DestinyLogo/>
                            <Container fixed={true} align="left">
                                <Typography variant="h5">D2 Flawed Flawless</Typography>
                            </Container>
                            <Container fixed={true} align="center">
                            <Container align="center">
                                <Paper
                                    component="form"
                                    sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: '50vw' }}
                                    onSubmit = {handleSumbit}
                                >
                                    <DoubleArrowIcon fontSize='large' color='#282c34'/>
                                    <InputBase
                                        sx={{ ml: 1, flex: 1 }}
                                        placeholder="Find a Guardian..."
                                        onChange = {handleChange}
                                        type="search"
                                    />
                                    <IconButton type="submit" sx={{ p: '10px' }}>
                                        <SearchIcon />
                                    </IconButton> 
                                </Paper>
                                </Container>
                            </Container>
                            <Container fixed={true} align="center"/>
                            <IconButton sx={{ ml: 1 }} onClick={colorMode.toggleColorMode} color="inherit">
                                {theme.palette.mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
                            </IconButton>
                            {theme.palette.mode}
                        </Toolbar>
                    </AppBar>
                </Box>
            </Container>
        </>
    );
  };


export default function ToggleColorMode() {
    const [mode, setMode] = React.useState('dark');
    const colorMode = React.useMemo(
      () => ({
        toggleColorMode: () => {
          setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
        },
      }),
      [],
    );
  
    const theme = React.useMemo(
      () =>
        createTheme({
          palette: {
            mode,
          },
        }),
      [mode],
    );
  
    return (
      <ColorModeContext.Provider value={colorMode}>
        <ThemeProvider theme={theme}>
          <AppSearchBar />
        </ThemeProvider>
      </ColorModeContext.Provider>
    );
  }